section.section1 {
	padding-top: 200px;
	padding-bottom: 20px;
	padding-left: clamp(10px, 5vw, 15px);
	padding-right: clamp(10px, 5vw, 15px);
	max-width: 900px;
	margin: auto;
	h1 {
		@include font($fontTitle, clamp(40px, 10vw, 80px), inherit);
	}
	div.container {
		display: flex;
		justify-content: center;
		gap: 20px;
	}
}
.card {
	width: 349px;
	height: 600px;

	display: flex;
	flex-direction: column;
	background: $highlightColor1;
	border-radius: 19px 19px 0px 0px;
	position: relative;
	overflow: auto;

	&-img {
		height: 200px;
		min-height: 200px;
		border-radius: inherit;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: inherit;
		}
	}
	&-header {
		&-title {
			display: flex;
			justify-content: space-between;
			span {
				&:nth-child(1) {
					@include font($fontTitle, 20px, inherit);
				}
				&:nth-child(2) {
					@include font($fontParagraphStyle, 18px, inherit);
					color: $secondaryColor1;
				}
			}
		}
	}
	&-body,
	&-header,
	&-footer {
		padding: 20px;
	}
	&-body {
		max-height: 300px;
		overflow: auto;
		overflow-x: hidden;
		-ms-overflow-style: none; /* IE and Edge */
		scrollbar-width: none; /* Firefox */
		&-description {
			@include font($fontParagraphStyle, 18px, 1.4);
			margin-top: 10px;
			overflow-wrap: break-word;
		}
	}
	&-body::-webkit-scrollbar {
		display: none;
	}
	&-footer {
		// position: absolute;
		// bottom: 0;
		// width: 100%;
		margin-top: auto;
		display: flex;
		justify-content: space-between;
		button {
			padding: 10px 10px;
			border: none;
			border-radius: 5px;
			background: $secondaryColor1;
			cursor: pointer;
			@include font($fontTitle, 20px, inherit);
			transition: background 200ms ease-in;
			&:hover {
				background: $primaryColor2;
			}
		}
		&-logo {
			width: 30px;
		}
	}
}
